'use client';

import { useState, type FormEvent } from 'react';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Textarea } from '@/components/ui/textarea';
import { Label } from '@/components/ui/label';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { useToast } from "@/hooks/use-toast";
import { Loader2, Send } from 'lucide-react'; // Import Send icon


export function Contact() {
  const [name, setName] = useState('');
  const [email, setEmail] = useState('');
  const [message, setMessage] = useState('');
  const [isLoading, setIsLoading] = useState(false);
  const { toast } = useToast();

  const handleSubmit = async (e: FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    setIsLoading(true);

    // Simulate form submission (replace with actual API call or Server Action later)
    await new Promise(resolve => setTimeout(resolve, 1500));

    console.log('Form submitted:', { name, email, message });

    setIsLoading(false);
    // Show success toast
     toast({
      title: "Message Sent!",
      description: "Thank you for reaching out. I'll get back to you soon.",
    });

    // Reset form fields
    setName('');
    setEmail('');
    setMessage('');
  };

  return (
    <section id="contact" className="w-full bg-background pb-24">
      <div className="container mx-auto px-4 md:px-6">
        <div className="mx-auto max-w-xl space-y-6 text-center">
           <h2 className="text-3xl font-bold tracking-tighter md:text-4xl/tight mb-4">Contact Me</h2>
           <p className="text-muted-foreground md:text-xl">
             Have a question, project idea, or just want to connect? Feel free to send me a message using the form below!
          </p>
        </div>
         <Card className="max-w-xl mx-auto mt-8 rounded-xl shadow-lg">
            <CardHeader>
                 <CardTitle className="text-2xl">Contact Form</CardTitle>
                 <CardDescription>Fill out the form below and I'll get back to you as soon as possible.</CardDescription>
            </CardHeader>
           <CardContent>
              <form onSubmit={handleSubmit} className="space-y-4">
                <div className="grid grid-cols-1 gap-4 sm:grid-cols-2">
                   <div className="space-y-2">
                    <Label htmlFor="name">Name</Label>
                    <Input
                      id="name"
                      type="text"
                      placeholder="Your Name"
                      value={name}
                      onChange={(e) => setName(e.target.value)}
                      required
                      className="rounded-md"
                     />
                   </div>
                  <div className="space-y-2">
                    <Label htmlFor="email">Email</Label>
                    <Input
                      id="email"
                      type="email"
                      placeholder="your.email@example.com"
                      value={email}
                      onChange={(e) => setEmail(e.target.value)}
                      required
                      className="rounded-md"
                    />
                   </div>
                </div>
                <div className="space-y-2">
                   <Label htmlFor="message">Message</Label>
                  <Textarea
                    id="message"
                    placeholder="Your message here..."
                    value={message}
                    onChange={(e) => setMessage(e.target.value)}
                    required
                    className="min-h-[120px] rounded-md"
                   />
                </div>
                <Button type="submit" disabled={isLoading} className="w-full rounded-full shadow-md hover:shadow-lg transition-shadow duration-300">
                  {isLoading ? (
                     <Loader2 className="mr-2 h-4 w-4 animate-spin" />
                  ) : (
                     <Send className="mr-2 h-4 w-4" />
                  )}
                   {isLoading ? 'Sending...' : 'Send Message'}
                </Button>
              </form>
           </CardContent>
         </Card>
      </div>
    </section>
  );
}
